import {
  assignAdminNavbar,
  fetchUrl,
  formatTimestampToAMPM,
  getDateFromTimestamp,
  getToken,
  logoutBtnOnClick,
  redirectTo,
  removeOptions,
} from "../util";
import { ROLES } from "../util/constants";
import { initSearch } from "../util/search";
import {
  XANO_ADMIN_SIGN_IN_REDIRECT,
  XANO_ADMIN_TEAM_DETAIL_SEARCH_URL,
  XANO_ADMIN_TEAM_DETAIL_URL,
} from "../util/xano_urls";

let UrlTeamId, UrlGymId;

window.onload = async function () {
  logoutBtnOnClick(XANO_ADMIN_SIGN_IN_REDIRECT, "sidebar-logout-btn");

  var curUrl = new URL(document.location.href);
  UrlTeamId = curUrl.searchParams.get("teamId") || null;

  if (!UrlTeamId) {
    // ? TODO: Change this to a modal showcasing the error
    console.error("No team id found");
    return;
  }

 const token = getToken(); // ? get token (saved from signin page)
  if (token) {
    let result = await fetchUrl(XANO_ADMIN_TEAM_DETAIL_URL + UrlTeamId, "GET", false, token);

    if (result.isFetchOk !== undefined && !result.isFetchOk) {
      console.error(result.message);
      // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
      return;
    }

    const { team_role, ...data } = result;

    if (team_role && (team_role === ROLES.SUPER_ADMIN || team_role === ROLES.ADMIN)) {
      
      assignAdminNavbar(team_role);
      getSuperAdminData(data, token);
    } else {
      redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=UNAUTHORIZED_USER`);
    }
  } else {
    // ? if there is no token
    redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
  }
};

async function getSuperAdminData(data, token) {
  const { sessions, team } = data;

  if (team.gyms.length > 0 && team.gyms[0].gym_id) {
    UrlGymId = team.gyms[0].gym_id;
  } else {
    console.error("No gym id found");
    return;
  }

  appendTeamDetail(team);
  appendSessions(sessions, team.name);
  initSearch(
    { data: sessions, token, API: XANO_ADMIN_TEAM_DETAIL_SEARCH_URL, extraXanoInputObj: [{ team_id: team.id }] },
    appendSessions,
    team.name
  );
}

function appendTeamDetail(team) {
  //   if (team.profile_picture) $("#team-detail-pfp").css("background-image", `url(${team.profile_picture.url})`);

  $("#team-detail-name").text(team.name);
  $("#team-detail-email").text(team.email);
  $("#team-detail-gender").text("NaN");
  $("#team-detail-number").text(team.contact_number);
  $("#team-detail-dob").text("NaN");
  //   $("#team-detail-dob").text(`${team.date_of_birth} (${calculateAgeFromDOB(team.date_of_birth)} Y/O)`);

  // ? TODO: change this to multiselect and change api to fetch all gyms too
  let selectGymEl = document.getElementById("team-detail-gym");
  removeOptions(selectGymEl);
  if (team.gyms.length === 0) {
    var opt = document.createElement("option");
    opt.value = "No gyms";
    opt.innerText = "No gyms";
    selectGymEl.appendChild(opt);
  } else {
    team?.gyms?.forEach((gym) => {
      var opt = document.createElement("option");
      opt.value = gym.id;
      opt.innerText = gym.location;
      selectGymEl.appendChild(opt);
    });
  }
}

function appendSessions(data, team_name) {
  const tableContainer = document.getElementById("table-container");
  const og_webflow_element = tableContainer.querySelector("#table-item");

  function appendSessionsFn(_data) {
    let allItems = tableContainer.querySelectorAll(".adb-user.w-inline-block");
    allItems.forEach((item, indx) => {
      if (indx !== allItems.length - 1) item.remove();
    });

    _data.forEach((session, indx) => {
      const clonedCard = og_webflow_element.cloneNode(true); // true means clone all childNodes and all event handlers
      clonedCard.setAttribute("id", "");
      clonedCard.setAttribute("href", `/admin/session?sessionId=${session.id}`);
      let el = clonedCard.querySelectorAll(".colcontent");

      if (indx % 2 === 0) clonedCard.style.backgroundColor = "#00edf4";

      el[1].innerText = session.session_name;
      el[2].innerText = team_name;
      el[3].innerText = session.code;
      el[4].innerText = getDateFromTimestamp(session.time);
      el[5].innerText = formatTimestampToAMPM(session.time, true);
      el[6].innerText = session?.users?.length ?? 0;
      el[7].innerText = session.status;

      clonedCard.style.display = "flex";
      tableContainer.prepend(clonedCard);
    });
  }

  appendSessionsFn(data);
  og_webflow_element.style.display = "none";
}


